"use client";

import type { HTMLAttributes, ReactNode } from "react";
import { forwardRef } from "react";
import { motion, type Variants } from "framer-motion";

const baseVariants: Variants = {
  hidden: { opacity: 0, y: 32 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.7,
      ease: [0.16, 1, 0.3, 1],
    },
  },
};

type SectionRevealProps = HTMLAttributes<HTMLDivElement> & {
  children: ReactNode;
  delay?: number;
  asChild?: boolean;
};

export const SectionReveal = forwardRef<HTMLDivElement, SectionRevealProps>(
  ({ children, className, delay = 0, asChild = false, ...rest }, ref) => {
    const Component = asChild ? motion.div : motion.div;

    return (
      <Component
        ref={ref}
        variants={baseVariants}
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, amount: 0.25 }}
        transition={{ delay, duration: 0.7, ease: [0.16, 1, 0.3, 1] }}
        className={className}
        {...rest}
      >
        {children}
      </Component>
    );
  },
);
SectionReveal.displayName = "SectionReveal";

type RevealSequenceProps = {
  children: ReactNode;
  className?: string;
  stagger?: number;
};

export function RevealSequence({
  children,
  className,
  stagger = 0.12,
}: RevealSequenceProps) {
  return (
    <motion.div
      className={className}
      variants={{
        hidden: {},
        visible: {
          transition: { staggerChildren: stagger },
        },
      }}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.2 }}
    >
      {children}
    </motion.div>
  );
}

